  display: block;
}

.tamanio50 {
  width: 50%;
}

h1, h2, h3 {
  color: #764E37;
  font-family: "Chewy", cursive;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
} /* UI */
.header {
  background-color: #FFD8EF;
  font-family: "Chewy", cursive;
  font-size: 1.75rem;
}
@media (min-width: 768px) {
  .header {
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .header {
    font-size: 3.5rem;
  }
}

.footer {
  color: #764E37;
  font-family: "Chewy", cursive;
  background-color: #FFD8EF;
}
.footer .contenedor-footer {
  display: grid;
  gap: 1rem;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .contenedor-footer {
    gap: 0;
    display: flex;
    flex-direction: row;
  }
}
.footer .copyright {
  grid-column: 1/2;
  grid-row: 1;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .footer .copyright {
    flex-basis: calc(60% - 1rem);
    display: flex;
    justify-content: center;
  }
}
.footer .copyright p {
  font-size: 1.5rem;
}
@media (min-width: 1024px) {
  .footer .copyright p {
    font-size: 2rem;
  }
}
.footer .derecha {
  grid-column: 2/3;
  grid-row: 1;
  display: grid;
}
@media (min-width: 768px) {
  .footer .derecha {
    display: flex;
    flex-basis: calc(40% - 1rem);
  }
}
.footer .derecha .social {
  grid-column: 1;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .footer .derecha .social {
    flex-basis: calc(40% - 1rem);
  }
}
.footer .derecha .social img {
  width: 20%;
}
@media (min-width: 768px) {
  .footer .derecha .social img {
    width: 15%;
  }
}
@media (min-width: 1024px) {
  .footer .derecha .social img {
    transition: width 0.5s;
  }
  .footer .derecha .social img:hover {
    cursor: pointer;
    width: 20%;
  }
}
.footer .derecha .telefono {
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .derecha .telefono p {
  margin: 0;
  font-size: 1.5rem;
}
@media (min-width: 1024px) {
  .footer .derecha .telefono p {
    font-size: 2rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  align-items: center;
  padding-bottom: 2.5rem;
  padding-top: 2.5rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    align-items: center;
  }
}
.barra a {
  display: flex;
  justify-content: center;
}

.logo {
  width: 50%;
}
@media (min-width: 768px) {
  .logo {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .logo {
    transition: 0.5s;
  }
  .logo:hover {
    width: 65%;
  }
}

.carrito {
  width: 25%;
}
@media (min-width: 768px) {
  .carrito {
    width: 40%;
  }
}
@media (min-width: 1024px) {
  .carrito {
    width: 30%;
    transition: 0.5s;
  }
  .carrito:hover {
    width: 35%;
  }
}

.navegacion {
  padding-top: 1rem;
}
.navegacion a {
  color: #764E37;
  text-decoration: none;
  display: flex;
  justify-content: center;
}
.navegacion a:last-of-type {
  padding-top: 1rem;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0rem;
    padding-right: 0rem;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    transition: font-size 0.5s ease;
  }
  .navegacion a:hover {
    font-size: 4rem;
  }
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}

